<template>
    <div class="cy_win">
        <div id="test"></div>
        <div class="line"></div>

<div class="codeArea">
<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;div id="test"&gt;&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import funcModule from "cy_static/funcModule";
    funcModule("#test", {
        "keyId" : "H5_Mall_Entry",
        "colSpace" : 0,
        "paddingTop" : 5,
        "paddingBottom" : 13,
        "backgroundColor" : "#ffffff"
    });
&lt;/script&gt;
</xmp >
</div>

        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="line"></div>
        <div class="cy_example">
            <iframe ref="iframe" src="?t=2#/runcode"></iframe>
        </div>
    </div>
</template>

<script>
    import {trim} from "../../../assets/js/codeStyle";
    import funcModule from "cy_static/funcModule";
    export default {
        name: 'funcModule',
        data () {
            return {
                msg: 'funcModule',
                cmOptions:{
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true

                },
                cmOptions1:{
                        readOnly:"true",
                        theme : "darcula",
                        mode : "text/html",  // 模式
                        indentUnit : 2,  // 缩进单位，默认2
                        smartIndent : true,  // 是否智能缩进
                        tabSize : 4,  // Tab缩进，默认4
                        showCursorWhenSelecting : true
                }
            }
        },
        mounted(){
            var m = this;  
            ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
                var dom, valDom, index = 0, getDom = function() {
                    index++;
                    dom = m.$refs[element + index];
                    valDom = m.$refs[element + "Val" + index];
                };
                getDom();
                while (dom) {
                    dom.codemirror.setValue(trim(valDom.innerHTML));
                    getDom();
                }
            });
            funcModule("#test", {
                data : [{"positionName":"H5新版商城首页功能球","adName":"所有分类","adDesc":"所有分类","adContent":"http://image.changyoyo.com/uploadimage/IMG20191018023349750.png","adUrl":"https://m.changyoyo.com/mall/?spm=3.1.3.P000001302.0.769991570762899041.02000000.02006827#/category","id":"P000006046","sort":5,"adWord":"所有分类","usePlat":"APP"},{"positionName":"H5新版商城首页功能球","adName":"卡券中心","adDesc":"卡券中心","adContent":"http://image.changyoyo.com/uploadimage/IMG20191018023358192.png","adUrl":"https://m.changyoyo.com/cymall/cardcenter.html","id":"P000006048","sort":4,"adWord":"卡券中心","usePlat":"APP"},{"positionName":"H5新版商城首页功能球","adName":"充值中心","adDesc":"充值中心","adContent":"http://image.changyoyo.com/uploadimage/IMG20191018023408891.png","adUrl":"https://m.changyoyo.com/lifePay/phone-traffic.html?needlogin=true&spm=3.1.102006.101118.P000003073.769991570762899041.02000000.02006827","id":"P000006047","sort":3,"adWord":"充值中心","usePlat":"APP"},{"positionName":"H5新版商城首页功能球","adName":"分期商城","adDesc":"分期商城","adContent":"http://image.changyoyo.com/uploadimage/IMG20191018023418942.png","adUrl":"https://m.changyoyo.com/event/2017/1222/index.html","id":"P000006054","sort":2,"adWord":"分期商城","usePlat":"APP"},{"positionName":"H5新版商城首页功能球","adName":"二手市场","adDesc":"二手市场","adContent":"http://image.changyoyo.com/uploadimage/IMG20191018023432918.png","adUrl":"https://m.changyoyo.com/manhattan2/index.html#/home","id":"P000006049","sort":0,"adWord":"二手市场","usePlat":"APP"}],
                "keyId" : "H5_Mall_Entry",
                "colSpace" : 0,
                "paddingTop" : 5,
                "paddingBottom" : 13,
                "backgroundColor" : "#ffffff"
            });
        }, 
        methods:{
            runcode(){
                var m = this, frame = m.$refs.iframe;
                frame.src = "?a=" + Math.random() + "#/runcode"
                frame.onload = function() {
                    frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
                }

            }
        }
    }
</script>

<style scoped>    
    .cy_win{display:flex;flex-flow:column;flex:1}
    .codeArea{height:220px;overflow: hidden;}
    .cy_example{position:static;flex:10}  
</style>


